.story-filter{
	width: 100%;
	display: block;
	margin-top: 50px;
	margin-bottom: 80px;
	&__header{
		font-size: 26px;
	}
	&__input{
		border: 2px solid #595959;
		border-radius: 0;
		background: white;
		height: auto;
		display: block;
	    -webkit-appearance: none;
	    -moz-appearance: none;
	    background-position: right 50%;
	    background-repeat: no-repeat;
	    background-image: url(data:image/png;base64,);
	    padding: .4em;
	    padding-left: 1em;
	    padding-right: 1.5em;
	    font-size: 19px;
		option{
			&:disabled{
				color: #666666;
				font-style: italic;
			}
			// background: white;
		}
	}
}