/* ---------------------------------------------------------------
Base styles
--------------------------------------------------------------- */
// #BetterNavigator {display: none;}
body, html {
  height: 100%;
}
html {
	font-size: 17px;
	line-height: 1.5;
	@media screen and (min-width: $break-small) {
		font-size: 19px;
		line-height: 1.6;
	}
}
body {
	background: #fdfdfd;
	color: #444;
	font-family: $body-font;
	margin: 0;
	overflow-x: hidden;
	padding: 0;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}


